import React, { Component } from "react";
import Slider from "react-slick";
import "./RankingSlide.css";
import RankingSlideData from './RankingSlideBAGData';
import { useState } from "react";
import { useNavigate, } from "react-router-dom";


export default function Responsive (){
  let navigate = useNavigate()
  let [res1, setRes1] = useState([56,57,58,59,60,61,62,63,64,65,66,67]);
    var settings = {
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 4,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true,
          },
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
          },
        },
      ],
    };
    return (

        <div className="first_slider">
          <Slider {...settings}>
            {RankingSlideData.map((a, i) => {
              return (
            <>
                <div className="imgbox" onClick={() => {navigate('/detail/'+res1[i])}}>
                  <div className="numberbox">{i+1}</div>
                  <img src={a.person} alt="" />
                  <img src={a.url} alt="" />
                </div>

                <div className="maintext_box" onClick={() => {navigate('/detail/'+res1[i])}}>
                  <p>{a.name}</p>
                  <span>{a.price}</span>
                </div>
            </>
              );
            })}
          </Slider>
        </div>

    );
}
