import Button from "react-bootstrap/Button";
import "./MlbStyling.css";
import MlbStyling_Data from "./MlbStylingData";

function MlbStyling(): JSX.Element{
  return (
    <div style={{ margin: "0 auto", width: "1275px" }}>
      <h2>MLB STYLING</h2>
      <ul className="Mlb_imgbox">
        {MlbStyling_Data.map((a) => {
          return (
            <li>
              <img src={a.url} alt="" />
              <div className="hover_bar">
                <Button variant="outline-light">더많은 스타일링 보기</Button>
              </div>
            </li>
          );
        })}
      </ul>
      <div className="mlb_sty_textbox">
        <h2>한번에 힙한 스타일링 완성! 키즈 상하세트</h2>
        <p>
          MLB KIDS 상하세트로 데일리룩을
          <br />
          간편하고 힙하게 완성해보세요
        </p>
        <Button variant="dark">더많은 스타일링 보기</Button>
      </div>
    </div>
  );
}

export default MlbStyling;
