import "./Collection.css";
import React, { useRef, useState } from "react";
// Import Swiper React components

import FreeTimeMenu1 from "./CollectionMenu1";
import FreeTimeMenu2 from "./CollectionMenu2";
import FreeTimeMenu3 from "./CollectionMenu3";
import FreeTimeMenu4 from "./CollectionMenu4";

// Import Swiper styles
import "swiper/css";
import "swiper/css/scrollbar";



import { useEffect } from "react";

export default function FreeTime(): JSX.Element{
  let [ta, setTa] = useState<number>(0);
  return (
    <>
      <div style={{ marginTop: "80px" }}>
        <div className="FreeTimebox">
          <div className="Free_title">
            <h2>COLLECTON</h2>
            <ul className="ok">
              <li
                style={{
                  color: "#fff",
                  border: "1px solid #000",
                  background: "#000",
                }}
                onClick={()=>{setTa(0)}}
              >
                페이즐리
              </li>
              <li onClick={()=>{setTa(1)}}>여름모자</li>
              <li onClick={()=>{setTa(2)}}>청키 마스크</li>
              <li onClick={()=>{setTa(3)}}>키즈 애슬레저</li>
            </ul>
            
          </div>

          <TabContent ta={ta}/>
        </div>
      </div>
    </>
  );
}


function TabContent({ta}:any): JSX.Element{
  let [fade, setFade] = useState('')
   useEffect( ()=>{
    setTimeout(()=>{ setFade('end')},20)
    return ()=>{
        setFade('')
    }
   } ,[ta])

  return (
    <div className={'start ' + fade}>
      { [<FreeTimeMenu1 />,<FreeTimeMenu2 />,<FreeTimeMenu3 />,<FreeTimeMenu4 />][ta] }
    </div>
  )
}
