import './Sns.css'
import "../Collection/Collection.css"
import Button from 'react-bootstrap/Button';
import SnsData from './SnsData';
import { useState } from 'react';
import axios from 'axios'

function Sns(){
  let [cout, stCount] = useState(1);
  let [co, stCnt] = useState(SnsData);
    return(
        <div className="FreeTimebox" id='SNS'>
          <div className="Free_title">
            <h2>MLB STYLE in SNS</h2>
            <ul>
              <li
                style={{
                  color: "#fff",
                  border: "1px solid #000",
                  background: "#000",
                }}
              >
                ALL
              </li>
              <li>MLB</li>
              <li style={{color:'#d28f8a',border:'1px solid #d28f8a'}}>KIDS</li>
            </ul>
          </div>

          <ul className='sn'>
                {co.map((a)=>{
                  return(<li><img src={a.url} alt=''/></li>)
                })}
          </ul>

        <Button variant="dark" style={{width:'120px'}}
         onClick={() => {
                 
          if(cout==1){
            axios.get('https://yeotaeho.github.io/2222-2222-222json/Sns.json').then((result)=>{
                let copy101 =[...SnsData, ...result.data];
                stCnt(copy101);
                stCount(2);
          })}else if(cout==2){
            axios.get('https://yeotaeho.github.io/json/Sns2.json').then((result)=>{
              let copy111 =[...co, ...result.data];
              stCnt(copy111);
              stCount(3);
             })   
          }

          if(cout===3){
            alert("더이상 상품이 없습니다.");  
          }
      }}
        >더보기</Button>
        </div>
    )
}
export default Sns
