import React, { useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import sliderData from "./sliderData";
import { Pagination,Navigation,Autoplay} from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

import "./slider.css";

export interface img {
    id:string;
    title: string;
    title2: string;
    desc:string;
    url:string;
  }

function Slider() {
    
  return (
    <>
      <Swiper
        slidesPerView={1}
        spaceBetween={0}
        loop={true}
        autoplay={{
            delay:5000,
        }}
        pagination={{
          clickable:true,
        }}
        navigation={true}
        modules={[Pagination, Navigation,Autoplay]}
        className="mySwiper"
      >
        {sliderData.map((a,i)=>{
            type className = string
            let slider_text:string = sliderData[i].id
          return (
            <SwiperSlide style={{position:'relative'}}>

              <p className="text_box">
                <span className={slider_text}>{sliderData[i].title}</span>
                <br/>
                <b className={slider_text}>{sliderData[i].title2}</b>
                <br/>
                <p className={slider_text}>{sliderData[i].desc}</p>
              </p>


            <img src={sliderData[i].url} alt=""/>
            </SwiperSlide>
          ) 
        })}
      </Swiper>
    </>
  );
}

export default Slider
