import React, { Component } from "react";
import Slider from "react-slick";
import "./Arrivals.css";
import Data from "./ArrivalsData";
import { useState } from "react";
import { useNavigate, } from "react-router-dom";
export interface ArrivalsData {
    id:number,
    person:string,
    url:string,
    name:string,
    price:string,
    img:string,
    img2:string|undefined,
}


export default function Arrivals (): JSX.Element{
  let navigate = useNavigate()
  let [res1, setRes1] = useState<Number[]>([0,1,2,3,4,5,6,7]);
    var settings = {
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 4,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true,
          },
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
          },
        },
      ],
    };
    return (
      <div className="New_Arrivals">
        <div className="first_slider">
          <h2> New Arrivals, New Life </h2>
          <Slider {...settings}>
            {Data.map((a,i) => {
                let person:string = a.person
                let url:string = a.url
                let name:string = a.name
                let price:string = a.price
              return (
                <div className="Arrivals_box" onClick={() => {navigate('/detail/'+res1[i])}}>
                  <img src={person} alt="" />
                  <img src={url} alt="" />
                  <p>{name}</p>
                  <span>{price}</span>
                </div>
              );
            })}
          </Slider>
        </div>
      </div>
    );

}
