import "./Cart.css";
import { Table } from "react-bootstrap";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { addCount, decreaseCount, deleteItem, sortName } from "../store";
interface RootState {
    cart: {
        id: number;
        imgurl: string;
        name: string;
        count: number;
      }[];
    }

function Cartssw() {
  let state = useSelector((state: RootState) => state);
  let dispatch = useDispatch();
 
  const smallProdcuctStyle = {
    border: "1px solid #ddd",
    width: "100px",
    height: "80px",
    cursor: "pointer",
  };

  const textVerticalAlign: React.CSSProperties = {
    verticalAlign: 'middle',
    textAlign: 'center',
  };

  return (
    <div className="C">
      <div className="cart">
        <h3>장바구니</h3>
        <p>Home {">"} 장바구니</p>
      </div>

      <div className="section">
        <ul className="dnl">
          <li>장바구니</li>
          <li>주문/결제</li>
          <li>주문완료</li>
        </ul>

        <ul className="asd">
          <li>일반주문</li>
          <li>예약주문</li>
          <li>매장픽업</li>
        </ul>

        <Table style={{ marginTop: "30px",padding:'30px 0',height:'auto'}}>
          <thead>
            <tr>
              <th style={{ verticalAlign: "middle", textAlign: "center" }}>
                id
              </th>
              <th>상품이미지</th>
              <th style={{ verticalAlign: "middle", textAlign: "center" }}>
                상품명
              </th>
              <th style={{ verticalAlign: "middle", textAlign: "center" }}>
                수량
              </th>
              <th style={{ verticalAlign: "middle", textAlign: "center" }}>
                변경하기
              </th>
            </tr>
          </thead>
          <tbody>



          {
              state.cart.map((a, i)=>
              <tr key={i}>
                  <td style={textVerticalAlign}>{state.cart[i].id+1}</td>


                  <td>
                   
                  <Link to={`/detail/${state.cart[i].id}`}>
                    <img src={`img/${state.cart[i].imgurl}`} style={smallProdcuctStyle} />
                  </Link>

                  </td>
                  <td style={textVerticalAlign}>{state.cart[i].name}</td>
                  <td style={textVerticalAlign}>{state.cart[i].count}</td>
                  <td style={textVerticalAlign}>
                    <Button onClick={()=>{
                       dispatch(addCount(state.cart[i].id))
                  }} variant="dark" style={{marginRight:'10px'}}>+</Button>

                  <Button onClick={()=>{
                       dispatch(decreaseCount(state.cart[i].id))
                  }} variant="dark" style={{marginRight:'10px'}}>-</Button>

                  <Button onClick={()=>{
                       dispatch(deleteItem(state.cart[i].id))
                  }} variant="dark">상품삭제</Button>
                
                  </td>              
              </tr>
              )
          }


          </tbody>
        </Table>
        <div style={{width:'100%',textAlign:'center'}}>
        <Button variant="dark" onClick={() => {
          dispatch(sortName());
        }}>이름순정렬</Button>
</div>
      </div>
    </div>
  );
}

export default Cartssw;
