import '../Ranking/RankingSlide.css';
import './Pick.css'
import React, { useState } from "react";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Button from 'react-bootstrap/Button';
import Pick_Data from './PickData';
import axios from 'axios';
import { useNavigate, } from "react-router-dom";

function Pick () {
  let [count, setCount] = useState(1);
  let [sc,set] = useState(Pick_Data)
  let navigate = useNavigate()
  let [res1, setRes1] = useState([100,101,102,103,104,105,106,107,108,109,110,111,112,113,114,115,116,117,118,119,120,121,122,123,124,125,126,127,128,129,130,131]);
  console.log(sc)
  
  return (

        <div className='mainS' style={{textAlign:'center'}}>
              <Navbar
          bg="none"
          style={{marginTop:'110px'}}
        >
          <Container style={{ maxWidth: "1298px"}}>
            <Navbar.Brand style={{fontSize:'32px'}}>
            MD'S PICK
            </Navbar.Brand>
            <Nav className="me-auto">
              <Nav.Link className="main" style={{color:'#767676',border:'none'}}>전체보기</Nav.Link>
            </Nav>
            <Nav>
              <Nav.Link className='main_ww' style={{color:'#000',borderBottom:'1px solid #000'}} onClick={()=>{}}>ALL</Nav.Link>
              <Nav.Link className='main_ww' onClick={()=>{}}>CAP</Nav.Link>
              <Nav.Link className='main_ww' onClick={()=>{}}>SHOES</Nav.Link>
              <Nav.Link className='main_ww' onClick={()=>{}}>APPAREL</Nav.Link>
              <Nav.Link className='main_ww' onClick={()=>{}}>BAG</Nav.Link>
              <Nav.Link className='main_ww' onClick={()=>{}}>KIDS</Nav.Link>
            </Nav>
          </Container>
        </Navbar>

        <ul>
            {sc.map((a,i)=>{
                return( 
                <li onClick={() => {navigate('/detail/'+res1[i])}}>
                    <img src={a.url} alt='' className='bbb'/>
                    <div className='pick_textbox'>
                        <p>{a.name}</p>
                        <span>{a.price}</span>
                    </div>
                </li>)
            })}
        </ul>
        <Button variant="dark" style={{marginTop:'50px'}}
        onClick={() => {
                 
          if(count==1){
            axios.get('https://yeotaeho.github.io/MLB_json/MLB.json').then((result)=>{
                let copy10 =[...Pick_Data, ...result.data];
                set(copy10);
                setCount(2);
       
          })}else if(count==2){
            axios.get('https://yeotaeho.github.io/MLB_json/MLB2.json').then((result)=>{
              let copy11 =[...sc, ...result.data];
              set(copy11);
              setCount(3);
             })   
          }

          if(count===3){
            alert("더이상 상품이 없습니다.");  
          }
      }}
        >상품더보기+
        </Button>
        </div>

  );
}

export default Pick
