import ArrlvalData from "./ArrlvalData2";
import "../Collection/Collection.css";
import { useState } from "react";
import { useNavigate, } from "react-router-dom";

function ArrlvalMenu2(){
  let navigate = useNavigate()
  let [res1, setRes1] = useState([96,97,98,99]);
    return(
        <div className="bg_img" id="Arrlval_back">
        <img src="./img/Arrlval_img2.jpg" alt="" />

        <div className="rightbox" id="Arrlval_right">
          {ArrlvalData.map((a,i) => {
            return (
              <div className="right_imgbox" onClick={() => {navigate('/detail/'+res1[i])}}>
                <img src={a.person} alt="" />
                <img src={a.url} alt="" />
                <p>{a.name}</p>
                <span>{a.price}</span>
              </div>
            );
          })}
        </div>
      </div>
    )
}

export default ArrlvalMenu2
